/*==============  Typography  ==============*/

h1, h2, h3, h4, h5, h6 {
    font-weight: 700;
    margin-bottom: 0;
    font-family: $font-alternate;
    line-height: 1.2;
    letter-spacing: -.3px;
    &+p,
    &+blockquote,
    &+ul,
    &+ol {
        margin-top: 20px; } }

h1 {
    font-weight: 800;
    @include fz(28);
    letter-spacing: -1.3px;
    @include mq-mf(sm) {
        @include fz(36); }
    @include mq-mf(lg) {
        @include fz(40);
        line-height: 1.1; }
    @include mq-mf(xl) {
        @include fz(45); } }

h2 {
    @include fz(26);
    @include mq-mf(lg) {
        @include fz(29); }
    @include mq-mf(xl) {
        @include fz(32); } }

h3 {
    @include fz(18);
    @include mq-mf(sm) {
        @include fz(21); }
    @include mq-mf(lg) {
        @include fz(23); }
    @include mq-mf(xl) {
        @include fz(26); } }

h4 {
    @include fz(16);
    @include mq-mf(sm) {
        @include fz(18); }
    @include mq-mf(lg) {
        @include fz(20); }
    @include mq-mf(xl) {
        @include fz(22); } }

h5 {
    @include fz(13);
    @include mq-mf(sm) {
        @include fz(14); }
    @include mq-mf(lg) {
        @include fz(16); }
    @include mq-mf(xl) {
        @include fz(18); } }

h6 {
    @include fz(10);
    @include mq-mf(sm) {
        @include fz(12); }
    @include mq-mf(lg) {
        @include fz(14); }
    @include mq-mf(xl) {
        @include fz(16); } }

p {
    margin-bottom: 0;
    &+h1,
    &+h2,
    &+h3,
    &+h4,
    &+h5,
    &+h6 {
        margin-top: 20px; }
    &+p,
    &+blockquote,
    &+ul,
    &+ol {
        margin-top: 15px; } }

blockquote {
    padding-left: 20px;
    position: relative;
    @include fz(16);
    cite {
        @include fz(14);
        font-style: normal;
        font-weight: 700;
        @include mq-mf(lg) {
            @include fz(16); } }
    &::before {
        content: "";
        display: block;
        position: absolute;
        top: 9px;
        left: 0;
        bottom: 7px;
        width: 1px;
        background-color: $color-accent; }
    @include mq-mf(lg) {
        @include fz(18); }
    @include mq-mf(xl) {
        @include fz(20); }
    &+h1,
    &+h2,
    &+h3,
    &+h4,
    &+h5,
    &+h6 {
        margin-top: 20px; }
    &+p,
    &+blockquote,
    &+ul,
    &+ol {
        margin-top: 15px; } }

ul {
    padding-left: 1em;
    list-style: none;
    margin-bottom: 0;
    li {
        position: relative;
        &::before {
            content: "";
            display: inline-block;
            position: absolute;
            color: inherit;
            left: -1em;
            font-weight: 700; } }
    &.unstyled {
        padding: 0;
        li {
            margin: 0;
            &::before {
                content: none; } } }
    &+h1,
    &+h2,
    &+h3,
    &+h4,
    &+h5,
    &+h6,
    &+p,
    &+ul,
    &+ol {
        margin-top: 15px; } }

ol {
    padding-left: 1em;
    margin-bottom: 0;
    &+h1,
    &+h2,
    &+h3,
    &+h4,
    &+h5,
    &+h6,
    &+p,
    &+ul,
    &+ol {
        margin-top: 15px; } }

a {
    color: $color-link;
    border-bottom: 1px solid #f1f1f1;
    transition: color .2s, border-color .2s;
    &:hover {
        color: $color-link;
        text-decoration: none;
        border-bottom: 1px solid $color-accent; } }
